.center{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 22vw;
    background: white;
    border-radius: 10px;
}

.center h1{
    text-align: center;
    padding: 0 0 20px 0;
    border-bottom: 1px solid silver;
}

.form{
    padding: 20px 40px;
    box-sizing: border-box;
}

.text_field{
    position: relative;
    border-bottom: 2px solid #adadad;
    margin: 30px 0;
}


.input{
    width: 100%;
    margin: 5px 0;
    height: 40px;
    font-size: 16px;
    border-bottom: 1px solid black;
    border-top: transparent;
    border-left: transparent;
    border-right: transparent;
    background: none;
    outline: none;
}

.label{
    position: relative;
    top: -65px;
    left: 5px;
    font-size: 16px;
    pointer-events: none;
}


.span::before{
    content: "";
    position: relative;
    top: 50px;
    left: 0;
    width: 0px;
    height: 2px;
    background: #2691d9;
    transition: .5s;
}

.input:focus{
    border-bottom: 1px solid #2691d9;
}

.input:focus ~ .label,
.input:valid ~ .label
{
    color: #2691d9;
}

.input:focus ~ .span::before,
.input:valid ~ .span::before{
    width: 100px;
}

.pass{
    margin: -5px 0 20px 5px;
    color: #a6a6a6;
    cursor: pointer;
}

.pass:hover{
    text-decoration: underline;
}

input[type="Submit"],input[type="Button"]{
    width: 40%;
    height: 50px;
    margin: 10px 6px;
    border:1px solid;
    border-radius: 25px;
    font-size: 18px;
    font-weight: 700;
    cursor: pointer;
}

input[type="Submit"]:hover,input[type="Button"]:hover{
    background: #2691d9;
    color: #e9f4fb;
    transition: .5s;
}

.signup_link{
    margin: 30px 0;
    text-align: center;
    font-size: 16px;
    color: #666666;
}

.signup_link a{
    color: #2691d9;
    text-decoration: none;
}

.signup_link a:hover{
    text-decoration: underline;
}

.HomeAbout{
    width: 100vw;
    height: 25vh;
}